import { useEffect } from 'react'
import { useLocation } from 'react-router-dom'

const ScrollToTop = () => {
    const { pathname } = useLocation();
useEffect(() => {
  setTimeout(() => {
    window.scrollTo(0, 0);
  }, 100); // 100~300ms 조정해보세요
}, [pathname]);


  return (
    <div>
    
    </div>
  )
}

export default ScrollToTop