import { useLocation, useNavigate } from 'react-router-dom';
import './Advance.scss'


const Payment = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const location = useLocation();


  const selectedCar = state?.car;
  const charge = state?.charge;

  const getUsageTime = (entry, exit) => {
    const start = new Date(entry);
    const end = new Date(exit);
    const diffMin = Math.floor((end - start) / 60000);
    const h = Math.floor(diffMin / 60);
    const m = diffMin % 60;
    return `${h}시간 ${m}분`;
  };

  const ver = {
    verticalAlign: 'bottom',
    paddingTop: '80px',
    fontWeight: 'bold'
  }

  return (

    <>
      <div className="page_nav_wrap">
        <div className="page_hoem">
          <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home"
            onClick={() => navigate('/')} />
        </div>
        <div className="page_one">
          <p onClick={() => navigate('/advance_guide')}>주차요금 사전결제</p>
        </div>
        <div className="page_list">
          <p onClick={() => navigate('/advance')}>사전결제 조회/결제</p>
        </div>
        <div className="page_list">
          <p onClick={() => navigate('/payment')}>사전 결제하기</p>
        </div>

      </div>
      <div className="common_wrap">
        <aside className='left'>
          <div className="sidebar_wrap">
            <div className="sidebar_title">
              <p>주차요금 사전결제</p>
            </div>
            <div className="sidebar_list">
              <p className={location.pathname === '/advance_guide' ? 'active' : ''}
                onClick={() => { navigate('/advance_guide') }}>사전결제 안내</p>
              <p className={location.pathname === '/advance' ? 'active' : ''}
                onClick={() => { navigate('/advance'); }}>사전결제 조회/결제</p>
            </div>
          </div>
        </aside>

        <aside className='right'>
          <div className="bang_title">
            <p>결제 내역</p>
          </div>

          <div className="bang_pay_wrap">
            <table className='bang_pay_table'>
              <tbody>
                <tr>
                  <th><p>이용 내역</p></th>
                  <td className='bang_check'>
                    <p>{selectedCar?.entry.split(' ')[0]}</p>
                    <p>{selectedCar?.location}</p>
                    <p>{selectedCar?.carNum}</p>
                  </td>
                  <td className='bang_sale'></td>
                  <td className='bang_pay' style={ver}>
                    <p>예상 주차 요금 {(charge?.parkingFee + charge?.evFee).toLocaleString()} 원</p>
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className='bang_check'>
                    <p>입차</p>
                    <p>주차 이용 시간</p>
                    <p>전기차 충전소</p>
                  </td>
                  <td className='bang_sale'></td>
                  <td className='bang_pay'>
                    <p>{selectedCar?.entry}</p>
                    <p>{charge ? getUsageTime(selectedCar.entry, selectedCar.exit) : '-'}</p>
                    <p>{selectedCar?.isEV && selectedCar?.chargeTime > 0 ? `${selectedCar.chargeTime}분` : '이용 내역 없음'}</p>
                  </td>
                </tr>
                <tr style={{ backgroundColor: '#D9D9D9' }}>
                  <th><p>결제 정보</p></th>
                  <td className='bang_check'>
                    <p>주차 요금</p>
                    <p>충전 요금</p>
                    <p>감면 및 할인</p>
                  </td>
                  <td className='bang_sale'>
                    <p>{charge?.hasDiscount ? `${selectedCar?.discounts} 50% ` : '없음'}</p>
                  </td>
                  <td className='bang_pay'>
                    <p>{charge?.parkingFee.toLocaleString()} 원</p>
                    <p>{charge?.evFee.toLocaleString()} 원</p>
                    <p>{charge?.hasDiscount ? `- ${(charge.parkingFee + charge.evFee - charge.totalFee).toLocaleString()} 원` : '0 원'}</p>
                  </td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <th><p>결제 금액</p></th>
                  <td className='bang_check'></td>
                  <td className='bang_sale'></td>
                  <th className='bang_pay'><p>{charge?.totalFee.toLocaleString()} 원</p></th>
                </tr>
              </tfoot>
            </table>

            <div className="bang_pay_btn">
              <button className='btn_1'>간편 결제</button>
              <button className='btn_1'>카드 결제</button>
            </div>
          </div>
        </aside>
      </div>
    </>

  );
};

export default Payment;
