import React from 'react'
import './Month.scss';
import { useLocation, useNavigate } from 'react-router-dom';

const Month = () => {
  const handleScrollTo = (id) => {
    const section = document.getElementById(id);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };
  const navigate = useNavigate();
  const location = useLocation();

  return (
    <div>

      <div className="page_nav_wrap">
        <div className="page_hoem">
          <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home"
            onClick={() => navigate('/')} />
        </div>
        <div className="page_one">
          <p onClick={() => navigate('/month')}>정기권</p>
        </div>
        <div className="page_list">
          <p onClick={() => navigate('/monthcash')}>정기권 안내</p>
        </div>
      </div>

      <div className="common_wrap">
        <aside className='left'>
          <div className="sidebar_wrap">
            <div className="sidebar_title">
              <p>정기권</p>
            </div>
            <div className="sidebar_list">
              <p className={location.pathname === '/month' ? 'active' : ''}
                onClick={() => navigate('/month')}>정기권 안내</p>
              <p className={location.pathname === '/monthcash' ? 'active' : ''}
                onClick={() => navigate('/monthcash')}>정기권 조회/결제</p>
            </div>
          </div>
        </aside>

        <aside className='right'>

          <div className="lee_Month_contents">
            <span className='lee_titleT lee_margin20'>정기권 안내</span>
            <span className='lee_titlesubT lee_margin20'>아래 유의사항을 반드시 읽으신 후 정기권 신청해 주시기 바라며, 유의 사항 미확인 및 오기재로 인해 발생하는 불이익은‘정기권 신청자 본인’에게 있습니다.</span>

            <nav className='lee_contentsnav'>
              <ul>
                <li onClick={() => handleScrollTo('section1')}>정기권 신청자격 및 배정제한</li>
                <li onClick={() => handleScrollTo('section2')}>정기권 이용</li>
                <li onClick={() => handleScrollTo('section3')}>정기권 감면</li>
                <li onClick={() => handleScrollTo('section4')}>정기권 변경</li>
                <li onClick={() => handleScrollTo('section5')}>정기권 결제 안내</li>
              </ul>
            </nav>
          </div>

          <div className="lee_Month_contents" id="section1">
            <span className='lee_titleT'>정기권 신청자격 및 배정제한</span>
            <ul className='lee_mainT'>
              <li>일반시민 누구나 가능합니다.</li>
              <li>여객자동차운수사업법, 화물자동차운수법의 적용 타 시도 소재 영업용 전세버스와 화물자동차의 경우에는 신청 불가합니다.</li>
              <li>1톤 초과차량, 캠핑카, 트레일러, 주차장별 높이제한 차량 등 주차장 환경에 적합하지 않은 차량은 정기권 신청 및 이용이 불가합니다.</li>
              <li>정기권 감면대상자 중 부정사용 2회 적발 시 주차요금 감면금액 전액 환수 및 정기권 배정 제한됩니다.</li>
              <li>1인 1대의 차량을 신청하지 아니한 경우(1대의 차량을 2인 이상이 신청한 경우) 정기권 배정 제한됩니다.</li>
              <li>정기권 순환추첨제 주차장의 경우 최대 1년까지만 사용가능하며, 사용기간이후 재신청 및 추첨을 통해 배정됩니다.</li>
            </ul>
          </div>

          <div className="lee_Month_contents" id="section2">
            <span className='lee_titleT'>정기권 신청방법</span>
            <span className='lee_mainT'></span>
            <div className='lee_infor'><img src={process.env.PUBLIC_URL + "/img/month_c1.png"} alt="imfor" /></div>
          </div>

          <div className="lee_Month_contents" id="section2">
            <span className='lee_titleT'>정기권 이용</span>
            <ul className='lee_mainT'>
              <li>이용기간은 매월1일~말일(23시59분)까지 입니다.</li>
              <li>정기권은 양도가 불가하며, 적발 시 자동취소 됩니다.</li>
              <li>주간정기권 이용 시간은 08~19시, 야간정기권 이용 시간은 18~09시입니다.</li>
              <li>주간,야간 월정기권 계약시간 외 주차 시 시간주차 요금을 적용합니다.</li>
              <li>월정기권 차량은 주차구획이 별도로 지정되어 있지 않으며, 주차장 만차 시 대기상황 발생 및 주차가 불가할 수 있습니다.</li>
              <li>공영주차장 차고지 방지를 위해 14일 이상 장기주차 시, 추후 정기권 신청에 제한이 있을 수 있습니다.</li>
              <li>이용하시고자 하는 주차장의 높이제한 규정을 반드시 확인 바랍니다.</li>
              <li>1톤 초과차량, 캠핑카, 트레일러, 주차장별 높이제한 차량 등 주차장 환경에 적합하지 않은 차량은 정기권 신청 및 이용이 불가합니다.</li>
            </ul>
          </div>

          <div className="lee_Month_contents" id="section3">
            <span className='lee_titleT'>정기권 감면</span>
            <span className='lee_titlesubT'>감면대상자의 경우, 정기권 등록 및 차량변경 시 적합한 증빙서류를 제출해야 감면혜택 적용이 가능합니다.
              (다만, 사용이 종료된 정기권에 대해서는 감면혜택 소급적용이 불가합니다). <br />
              개인정보보호법 시행에 따라 증빙서류 제출 시, 개인정보 관련 해당사항(주민번호 뒷자리, 동 이하 주소 등)을 마스킹하여 제출하셔야 합니다.</span>
            <div className='lee_infor'><img src={process.env.PUBLIC_URL + "/img/month_c2.png"} alt="imfor" /></div>
          </div>

          <div className="lee_Month_contents" id="section4">
            <span className='lee_titleT'>정기권 변경</span>
            <ul className='lee_mainT'>
              <li>해당하는 필요서류를 제출하실 경우, 정기등록 차량번호 변경이 가능합니다.</li>
              <li>감면적용 가능한 차량으로 변경하는 경우 필요서류와 함께 감면적용 제출서류도 포함해 제출 하여 주시기 바랍니다.</li>
              <li>정기등록 차량 변경 시 사전에 변경서류를 제출하고 변경 등록 조치를 받으신 후 이용해야 합니다. ※ 사전변경 없이 이용 시 시간주차 요금 적용</li>
              <li>정기등록 차량 변경 시 적합한 서류제출을 통해 이용기간 중 월 1회에 한해 가능합니다</li>
            </ul>
            <div className='lee_infor'><img src={process.env.PUBLIC_URL + "/img/month_c3.png"} alt="imfor" /></div>
          </div>

          <div className="lee_Month_contents" id="section5">
            <span className='lee_titleT'>정기권 결제방법</span>
            <ul className='lee_mainT'>
              <li>정기권 납부일은 매월 20일경부터 말일까지이며, 미결제시 월정기권은 자동취소 됩니다.</li>
              <li>홈페이지 납부(온라인결제)</li>
              <li>출구 무인정산 또는 사전정산기 결제</li>
              <li>사전정산기가 없는 주차장의 경우 출구에서 결제 가능</li>
              <li>가상계좌의 경우 매달 변경되므로 문자 확인 후 이체 (입금자명 차량번호 확인)</li>
            </ul>
          </div>

          <div className="lee_Month_contents">
            <span className='lee_titleT'>정기권 환불</span>
            <ul className='lee_mainT'>
              <li>정기권 취소 환불은 가능하며, 환불신청일 익일부터 처리가 가능합니다.(신청일까지 사용한 것으로 간주)</li>
              <li>수원시주차장조례 제9조 및 공정거래위원회 주차장관리규정 표준약관 6항에 의거하여 환불처리를 해드리고 있습니다.</li>
            </ul>
            <div className='lee_infor'><img src={process.env.PUBLIC_URL + "/img/month_c4.png"} alt="imfor" /></div>
          </div>
        </aside>

      </div>

    </div>
  )
}

export default Month