import { useSelector } from "react-redux"
import './Home.scss';
import './Lee_common.scss';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/free-mode';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Autoplay, FreeMode, Navigation, Pagination } from 'swiper/modules';
import { useNavigate } from "react-router-dom";

const Home = () => {
    const park = useSelector((state) => state.park);
    const notic = useSelector((state) => state.notic);
    const qna = useSelector((state) => state.qna);
    const navigate = useNavigate();

    return (
        <div className='lee_home'>
            <div className="lee_home_swiper">
                <Swiper
                    spaceBetween={0}
                    centeredSlides={true}
                    loop={true}
                    pagination={{
                        clickable: true,
                    }}

                    modules={[Autoplay, Pagination]}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    className="mySwiper hometopswiper"
                >
                    <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/banner1.png"} alt="home_img" /></SwiperSlide>
                    <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/banner2.png"} alt="home_img" /></SwiperSlide>
                </Swiper>
            </div>

            <div className="lee_home_contents_menu lee_infor">
                <span className='lee_titleT_20margin'>빠른 보기 메뉴</span>
                <span className='lee_titlesubT'>많이 사용하는 기능들만 보아봤어요</span>

                <div className="lee_home_menu_icon lee_infor">
                    <div className="lee_home_menu_icon_box" onClick={() => navigate('/month')}>
                        <img src={process.env.PUBLIC_URL + "/img/home_i1.png"} alt="home_img" />
                        <span>정기권 조회/결제</span>
                    </div>
                    <div className="lee_home_menu_icon_box" onClick={() => navigate('/advance_guide')}>
                        <img src={process.env.PUBLIC_URL + "/img/home_i2.png"} alt="home_img" />
                        <span>사전결제 조회/결제</span>
                    </div>
                    <div className="lee_home_menu_icon_box" onClick={() => navigate('/open/guide')}>
                        <img src={process.env.PUBLIC_URL + "/img/home_i3.png"} alt="home_img" />
                        <span>이용 및 감면 혜택 안내</span>
                    </div>
                    <div className="lee_home_menu_icon_box" onClick={() => navigate('/map_search')}>
                        <img src={process.env.PUBLIC_URL + "/img/home_i4.png"} alt="home_img" />
                        <span>주차장 찾기</span>
                    </div>
                </div>

            </div>

            <div className="lee_home_contents_map lee_infor">
                <div className="lee_map_part">
                    <iframe
                        src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d7550.611589456231!2d127.01451385069774!3d37.27993670554778!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sko!2skr!4v1750902478828!5m2!1sko!2skr"
                        width="100%"
                        height="600"
                        style={{ border: 0 }}
                        allowFullScreen=""
                        loading="lazy"
                        referrerPolicy="no-referrer-when-downgrade"
                        title='지도'
                    />

                    <div className="lee_home_livepark">
                        <span className='lee_maptitleT'>실시간 주차현황</span>
                        <span className='lee_mapsubtitleT'>가장 빠르게 전달해드려요</span>
                        <Swiper
                            slidesPerView={3}
                            spaceBetween={0}
                            freeMode={true}
                            loop={true}
                            pagination={{
                                clickable: true,
                            }}
                            autoplay={{
                                delay: 2000,
                                disableOnInteraction: false,
                            }}
                            navigation={true}
                            modules={[Autoplay, Navigation, FreeMode]}
                            className="mySwiper"
                        >
                            {park.map((item, index) => (
                                <SwiperSlide key={index}>
                                    <div className="lee_home_liveparkbox">
                                        <span className='lee_map_inner_boxtitleT ellipsis'>{item.title}</span>
                                        <span className='lee_cir'>
                                            <img src={process.env.PUBLIC_URL + item.color} alt="circle" />
                                        </span>
                                        <span className='lee_map_innersub_bigT'>현재 {item.parkingCount}가능</span>
                                        <span className='lee_map_innersubT'>전체 {item.parkingSpace}면</span>

                                        <div className="lee_home_liveparkbox_innerbox">
                                            <span className="lee_home_liveparkbox_innerbox2_titleT">주차 이용구역 현황</span>
                                            <div className="lee_home_liveparkbox_innerbox2">
                                                <span className="lee_home_liveparkbox_innerbox2_subT">장애인 {item.disabledPreson}대</span>
                                                <span className="lee_home_liveparkbox_innerbox2_subT">여성 {item.womenpark}대</span>
                                            </div>
                                        </div>

                                        <div className="lee_home_liveparkbox_innerbox">
                                            <span className="lee_home_liveparkbox_innerbox2_titleT">전기차 충전소 현황</span>
                                            <div className="lee_home_liveparkbox_innerbox2">
                                                <span className="lee_home_liveparkbox_innerbox2_subT">급속 {item.electricCharge}대</span>
                                                <span className="lee_home_liveparkbox_innerbox2_subT">완속 1대</span>
                                            </div>
                                        </div>
                                    </div>
                                </SwiperSlide>
                            ))
                            }
                        </Swiper>
                        <img className='lee_circle' src={process.env.PUBLIC_URL + "/img/home_livepark_icon1.png"} alt="home_img" />
                    </div>
                </div>

            </div>
            <div className="home_under_wrap lee_infor ">
                <div className="lee_home_notice">
                    <span className="lee_titleT">공지사항</span>
                    <span className="lee_titlesubT">새로운 소식을 확인하세요</span>
                    <button className="btn_2" onClick={() => { navigate('/open/notic') }}>더보기 +</button>
                    <div className="home_notice_list">
                        {
                            notic.slice(0, 5).map((item, index) => (
                                <div className="home_map_flex">
                                    <div className="home_notice_inner_box">
                                        <span className="home_notice_id">{item.id + 1}</span>
                                        <span className="home_notice_title_hover">{item.title}</span>
                                    </div>
                                    <span>{item.date}</span>
                                </div>
                            )
                            )}
                    </div>
                </div>

                <div className="lee_home_qna">
                    <span className="lee_titleT">자주하는 질문</span>
                    <span className="lee_titlesubT">전화문의보다 빠르게 정보를 알 수 있어요</span>
                    <button className="btn_2"  onClick={() => { navigate('/open/qna') }}>더보기 +</button>
                    <div className="home_qna_list">
                        {qna.slice(0, 5).map((item, index) => (
                            <div className="home_map_flex">
                                <span className="home_notice_id">{item.id + 1}</span>
                                <span className="home_notice_title_hover">{item.title}</span>
                            </div>
                        ))
                        }

                    </div>

                </div>
            </div>

        </div>
    )
}

export default Home