import { useLocation, useNavigate } from 'react-router-dom';
import './Advance.scss'



const AdvanceGuide = () => {
    const navigate = useNavigate();
    const location = useLocation();

    const fcolor = {
        color: '#EE334E',
        fontSize: '16px'
    }

    return (
        <div>
            <div className="page_nav_wrap">
                <div className="page_hoem">
                    <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home" 
                    onClick={()=>navigate('/')}/>
                </div>
                <div className="page_one">
                    <p onClick={()=>navigate('/advance_guide')}>주차요금 사전결제</p>
                </div>
                <div className="page_list">
                    <p onClick={()=>navigate('/advance_guide')}>사전결제 안내</p>
                </div>
            </div>
            <div className="common_wrap">

                <aside className='left'>
                    <div className="sidebar_wrap">
                        <div className="sidebar_title">
                            <p>주차요금 사전결제</p>
                        </div>
                        <div className="sidebar_list">
                            <p className={location.pathname === '/advance_guide' ? 'active' : ''}
                                onClick={() => { navigate('/advance_guide') }}>사전결제 안내</p>
                            <p className={location.pathname === '/advance' ? 'active' : ''}
                                onClick={() => { navigate('/advance'); }}>사전결제 조회/결제</p>
                        </div>
                    </div>
                </aside>

                <aside className='right'>

                    <div className="bang_title">
                        <p>사전 결제 안내</p>
                    </div>

                    <div className="bang_sub_title">
                        <p>주차요금 사전 결제란?</p>
                        <p>주자창에서 출차하기 전에 주차요금을 사전 결제하여 빠르고 편리하게 주차장을 이용하는 서비스 입니다.<br />
                            홈페이지 또는 어플리케이션을 통하여 주차요금을 사전에 결제하고 별도의 절차 없이 출차하는 서비스 입니다.</p>
                    </div>


                    <div className="bang_guide_wrap">

                        <div className="bang_guide">
                            <img src={process.env.PUBLIC_URL+"/img/bang1.png"} alt="advance" />
                            <div className="bang_guide_text">
                                <p>홈페이지 접속 및 앱스토어 , 구글 플레이 등을<br /> 통해
                                    ‘수차차’ 를 다운로운하여 접속해주세요</p>
                            </div>
                        </div>

                        <div className="bang_guide">

                            <div className="bang_guide_text">
                                <p>사전결제조회로 이동하여
                                    현재 주차하신<br /> 주차장과
                                    주차하신 차량의 번호를 확인하여<br /> 주세요.</p>
                                <p style={fcolor}>(주차하신 차량의 번호를 전체 입력하여<br />
                                    검색하여 주세요 ex)12가 1234) </p>
                            </div>
                            <img src={process.env.PUBLIC_URL+"/img/bang2.png"} alt="advance" />

                        </div>
                        <div className="bang_guide">
                            <img src={process.env.PUBLIC_URL+"/img/bang3.png"} alt="advance" />
                            <div className="bang_guide_text">
                                <p>결제하신 내역을 확인 후 카드 등록 및 <br />간편결제를
                                    이용하여 사전 정산을 완료하시고 출차해주세요</p>
                                <p style={fcolor}>사전 결제 후 30분 이내에 출차 완료하시지<br /> 않을 경우
                                    출차시 추가 요금이 부과됩니다.
                                </p>
                            </div>
                        </div>
                    </div>
                </aside>
            </div>
        </div>


    )
}

export default AdvanceGuide