import { useState } from 'react'
import '../AdressSearch.scss';
import { Button,Modal, Table } from 'react-bootstrap'
import { useLocation, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';

const AdressSearch = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const [jin_tab, setJin_tab] = useState(0)
  const park = useSelector(state=>state.park)

  
  const [searchInput, setSearchInput] = useState(''); // 입력 중인 값
  const [searchTerm, setSearchTerm] = useState(''); // 실제 검색 조건

  const [selectedGu, setSelectedGu] = useState('전체'); //먼저 선언!
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 5;

  const handleSearch = () => {
    setSearchTerm(searchInput);
    setCurrentPage(1);
  };

  const filteredList = park.filter(item => {
    const matchSearch = item.title.includes(searchTerm) || item.adress.includes(searchTerm);
    // 구 단위 필터링
    const matchGu = selectedGu === '전체' || item.adress.includes(selectedGu);
    return matchSearch && matchGu;
  });  

  const totalPages = Math.ceil(filteredList.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const paginatedList = filteredList.slice(startIndex, startIndex + itemsPerPage);

  // 모달창
  const [selectedItem, setSelectedItem] = useState(null);
  const [showModal, setShowModal] = useState(false);

  return (
    <>

      <div className="page_nav_wrap">
        <div className="page_hoem">
          <img src={process.env.PUBLIC_URL + "/img/home_icon.png"} alt="home"  onClick={()=>navigate('/')}/>
        </div>
        <div className="page_one">
          <p onClick={()=>navigate('/map_search')}>주차장 찾기</p>
        </div>
        <div className="page_list">
          <p onClick={()=>navigate('/adress_search')}>주소 검색</p>
        </div>
      </div>

      <div className="common_wrap">

        <aside className='left'>
          <div className="sidebar_wrap">
            <div className="sidebar_title">
              <p>공영주차장</p>
            </div>
            <div className="sidebar_list">
              <p className={location.pathname === '/map_search' ? 'active' : ''}
                onClick={() => navigate('/map_search')}>지도 검색</p>
              <p className={location.pathname === '/adress_search' ? 'active' : ''}
                onClick={() => navigate('/adress_search')}>주소 검색</p>
            </div>
          </div>
        </aside>
        
        <aside className='right'>
          <h4 className='mainTitle_jin'>공영주차장</h4>
          <p className='subTitle_jin'>주소검색</p>

          <div className="contents_jin">

            <div>
              {/* 주차장 정보 테이블 */}
              <Table hover borderless>
                <thead>
                  <tr>
                    <th>주차장명</th>
                    <th>주소</th>
                    <th>기본요금</th>
                    <th>추가요금</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    paginatedList.map((inf, i) => (
                      <tr key={inf.id} onClick={() => {
                        setSelectedItem(inf);
                        setShowModal(true);
                      }}>
                        <td>{inf.title}</td>
                        <td><p>{inf.adress}</p></td>
                        <td>2,000원</td>
                        <td>1,000원(30분)</td>
                      </tr>
                    ))
                  }
                </tbody>
              </Table>

              <div className="pages_jin">
                <Button variant="secondary" size='sm'                 
                onClick={() => 
                setCurrentPage(prev => Math.max(prev-1,1))}><img src={process.env.PUBLIC_URL + "/img/arrow-left.png"} /></Button>
                {
                  Array.from({length: totalPages}, (_, i) => (
                    <Button key={i} variant="secondary" size='sm'
                    className={currentPage === i + 1 ? 'click_jin' : ''}
                    onClick={() => setCurrentPage(i + 1)}
                    >{i + 1}</Button>
                  ))
                }
                <Button variant="secondary" size='sm'
                onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}><img src={process.env.PUBLIC_URL + "/img/arrow-right.png"} /></Button>
              </div>
            </div>

            <div>
              <div className="search_jin">
                <input type="search" placeholder='주차장명 또는 주소입력' value={searchInput}
                onChange={(e) => setSearchInput(e.target.value)}
                onKeyDown={(e) => {
                  if(e.key === 'Enter') handleSearch();
                }} />

                <label htmlFor="search"
                onClick={handleSearch}>조회</label>
              </div>

              <div className="area_jin">
                <Button variant="outline-danger"
                  onClick={() => {
                    setSelectedGu('권선구');
                    setCurrentPage(1);
                  }}>권선구</Button>
                <Button variant="outline-danger"
                  onClick={() => { setSelectedGu('영통구'); setCurrentPage(1); }}>영통구</Button>
                <Button variant="outline-danger"
                  onClick={() => { setSelectedGu('장안구'); setCurrentPage(1); }}>장안구</Button>
                <Button variant="outline-danger"
                  onClick={() => { setSelectedGu('팔달구'); setCurrentPage(1); }}>팔달구</Button>
              </div>

              <Button variant="danger"
              onClick={() => setSelectedGu('전체')}>전체</Button>
            </div>

          </div>
        </aside>

      </div>

      {/* 모달창 */}
      {showModal && selectedItem && (
        <div className="modal show_jin" style={{ display: 'block' }}>
          <div className='modalPosition_jin'>
            <Modal.Dialog>
              <Modal.Header>
                <Modal.Title>{selectedItem.title}</Modal.Title>
                <img
                  src={process.env.PUBLIC_URL + "/img/close-button.png"}
                  onClick={() => setShowModal(false)}
                />
              </Modal.Header>

              <Modal.Body>
                <Table bordered>
                  <tbody>
                    <tr>
                      <td>주차장</td>
                      <td>{selectedItem.title}</td>
                    </tr>
                    <tr>
                      <td>공영 주차장 주소</td>
                      <td>{selectedItem.adress}</td>
                    </tr>
                    <tr>
                      <td>운영시간</td>
                      <td>{selectedItem.openHours}</td>
                    </tr>
                    <tr>
                      <td>주차요금</td>
                      <td>{selectedItem.parkFee}</td>
                    </tr>
                    <tr>
                      <td>월 정기권 금액</td>
                      <td>{selectedItem.season}</td>
                    </tr>
                    <tr>
                      <td>연락처</td>
                      <td>{selectedItem.phone}</td>
                    </tr>
                    <tr>
                      <td>실시간 주차정보</td>
                      <td>
                        전체면수: {selectedItem.parkingSpace}&nbsp;
                        주차가용대수: {selectedItem.parkingCount}
                      </td>
                    </tr>
                    <tr>
                      <td>주차면수</td>
                      <td>
                        장애인: {selectedItem.disabledPreson}면&nbsp;
                        경차: {selectedItem.smCar}면&nbsp;
                        전기차: {selectedItem.electricCharge}면
                      </td>
                    </tr>
                  </tbody>
                </Table>
              </Modal.Body>
            </Modal.Dialog>
          </div>
        </div>
      )}


    </>
  )
}

export default AdressSearch