import { configureStore } from "@reduxjs/toolkit";
import carReducer from "./states/car";
import chargeReducer from "./states/charge";
import parkReducer from "./states/park";
import noticReducer from "./states/notic";
import qnaReducer from "./states/qna";


export default configureStore ({

    reducer : {
        car : carReducer ,
        charge : chargeReducer ,
        park : parkReducer ,
        notic : noticReducer ,
        qna : qnaReducer
    }
})