import React, { useEffect, useState } from 'react'
import './OpenSpace.scss'
import { Accordion, Form, Navbar, Table, Button, Card, Pagination } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'
// import Pagination from 'react-bootstrap/Pagination';


const OpenSpace = () => {
    const { sub } = useParams();

    const [young_tab, setYoung_tab] = useState(1)
    const qna = useSelector(state => state.qna)
    const noticList = useSelector(state => state.notic)

    useEffect(() => {
        if (sub === 'guide') {
            setYoung_tab(0)
        } else if (sub === 'notic') {
            setYoung_tab(1)
        } else if (sub === 'qna') {
            setYoung_tab(2)
        }
    },[sub])

    // 공지사항 UI 상태들
    const [listOk, setListOk] = useState(true);  // 게시글 전체리스트
    const [readOk, setReadOk] = useState(false);  // 게시글 읽기
    const [boardInfo, setBoardInfo] = useState({});  // 현재 읽고 있는 게시글의 정보 (제목, 내용 등)를 저장하는 상태
    const tabNames = ['이용안내', '공지사항', '자주 묻는 질문(FAQ)'];
    const navigate = useNavigate();

    // 페이지네이션
    const [page, setPage] = useState([1, 1]);
    const [lastpage, setLastpage] = useState([1, 1]);

    let items = { 0: [], 1: [] };
    for (let i in page) {

        for (let number = 1; number <= lastpage[i]; number++) {
            items[i].push(
                <Pagination.Item key={number} active={number === page[i]}
                    onClick={() => handlePageChange(i, number)}>
                    {number}
                </Pagination.Item>,
            );
        }
    }

    // 페이지 번호 클릭 시 호출하는 함수
    const handlePageChange = (index, pageNumber) => {
        const newPage = [...page];
        newPage[index] = pageNumber;
        setPage(newPage);
    };

    useEffect(() => {
        const noticcount = Math.ceil(noticList.length / 5);
        const qnacount = Math.ceil(qna.length / 5);
        setLastpage([noticcount, qnacount]);
    }, [page])

    // FAQ토글
    const [activeKey, setActiveKey] = useState(null);
    const handleToggle = (eventKey) => {
        setActiveKey(prevKey => (prevKey === eventKey ? null : eventKey));
    };

    // 게시글 목록 보기
    const boardListView = () => {
        setReadOk(false);
        setListOk(true);
    };

    // 게시글 읽기
    const boardRead = (id) => {
        setListOk(false);
        setReadOk(true);
        const selectedBoard = noticList.find(notic => notic.id === id);
        // console.log(selectedBoard)
        setBoardInfo(selectedBoard);
    }

    return (

        <>

            <div className="page_nav_wrap">
                <div className="page_hoem">
                    <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home" onClick={() => navigate('/')} />
                </div>
                <div className="page_one">
                    <p onClick={() => { setYoung_tab(0) }}>열린 공간</p>
                </div>
                <div className="page_list">
                    <p>{tabNames[young_tab]}</p>
                </div>
            </div>

            <div className='common_wrap'>
                <div className="left">
                    <div className="sidebar_wrap">
                        <div className="sidebar_title">
                            <p>열린공간</p>
                        </div>
                        <div className="sidebar_list">
                            <p className={young_tab === 0 ? 'active' : ''} onClick={() => {
                                setYoung_tab(0);
                                navigate('/open/guide')
                             }}>이용안내</p>
                            <p className={young_tab === 1 ? 'active' : ''} onClick={() => {
                                setYoung_tab(1);
                                navigate('/open/notic')
                            }}>공지사항</p>
                            <p className={young_tab === 2 ? 'active' : ''} onClick={() => {
                                 setYoung_tab(2)
                                 navigate('/open/qna') }}>자주 묻는 질문(FAQ)</p>
                        </div>
                    </div>
                </div>

                <div className="right">
                    {
                        young_tab === 1 ? (
                            <div className="young_notice">
                                <h2>공지사항</h2>
                                {/* 목록 보기 */}
                                {listOk && (
                                    <>
                                        <Table striped bordered hover className='no'>
                                            <thead>
                                                <tr>
                                                    <th>번호</th>
                                                    <th>제목</th>
                                                    <th>작성자</th>
                                                    <th>작성일자</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    noticList.slice((page[0] - 1) * 5, page[0] * 5).map((notic, i) =>
                                                        <tr key={i}>
                                                            <td><p>{notic.id + 1}</p></td>
                                                            <td onClick={() => boardRead(notic.id)}>{notic.title}</td>
                                                            <td>{notic.writer}</td>
                                                            <td>{notic.date}</td>
                                                        </tr>
                                                    )
                                                }
                                            </tbody>
                                        </Table>

                                        <Pagination size="sm">{items[0]}</Pagination>
                                    </>

                                )}


                                {/* 내용 읽기 */}
                                {readOk && (
                                    <>
                                        <div className='text-end mb-3'>
                                            <button className='btn_1' onClick={boardListView}>목록으로</button>
                                        </div>


                                        <div className="notic_body_title">
                                            <p>{boardInfo.title}</p>
                                        </div>

                                        <div className="notic_wrap">
                                            <table className='notic_body'>
                                                <tbody>
                                                    <tr>
                                                        <th>작성자</th>
                                                        <td>{boardInfo.writer}</td>
                                                    </tr>

                                                    <tr>
                                                        <th>작성일자</th>
                                                        <td>{boardInfo.date}</td>
                                                    </tr>
                                                </tbody>

                                                <tfoot>
                                                    <tr>
                                                        <td colSpan={2}>
                                                            <img src={process.env.PUBLIC_URL+boardInfo.body} alt='img' width="100%" />
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>

                                        </div>

                                        {/* <Card>
                                            <Card.Header>{boardInfo.title}</Card.Header>
                                            <Card.Body>
                                                <Card.Text><img src={boardInfo.body} alt='img' width="100%" /></Card.Text>
                                            </Card.Body>
                                        </Card> */}

                                    </>
                                    // <div className='mt-3'>
                                    //     <h5 className='text-start'>{boardInfo.title}</h5>
                                    //     <hr />
                                    //     <p className='text-start'>{boardInfo.description}</p>
                                    //     <div className='text-end mt-3'>
                                    //         <Button variant="secondary" onClick={boardListView}>목록으로</Button>
                                    //     </div>
                                    // </div>
                                )}
                            </div>
                        ) : (
                            young_tab === 0 ? (
                                <div className="young_guide">
                                    <h1>이용구분</h1>
                                    <ul>
                                        <li>· 일반이용 : 주차장 이용 후 출차 시마다 요금을 납부하여 이용</li>
                                        <li>· 정기권이용 : 월 정기권 주차권 이용자로 매월 요금을 선납하여 이용</li>
                                    </ul>

                                      <div className='young_Sti'>
                                        <h3><a onClick={()=>{
                                            navigate('/month')
                                        }} style={{color:'#fff'}}>정기권 바로가기</a></h3>
                                    </div>

                                    <h2>이용안내</h2>
                                    <ul>
                                        <li>· 수원도시공사 운영 모든 공영주차장은 카드전용 주차장임</li>
                                        <li>· 통합관제센터(695-0930)를 통해 365일 24시간 민원응대 가능함<br />
                                            ※예외) 노상주차장, 화물주차장, 농수산물도매시장 주차장은 주차관리원 근무시에만 민원응대 가능</li>
                                        <li>· 1톤 초과차량, 캠핑카, 트레일러, 주차장별 높이제한 차량 등 주차장 환경에 적합하지 않은 차량은 주차장 이용이 불가함</li>
                                        <li>· 주차장 만차 시 주차장 이용이 불가함</li>
                                        <li>· 공영주차장 내 설치된 장애인면, 환경친화적자동차(전기차, 수소차, 하이브리드) 주차구역, 전기차 충전구역 등에 일반차량 주차 시 과태료 부과대상임</li>
                                        <li>· 주차장 내 취사행위, 야영행위 등은 금지되며 과태료 부과대상임</li>
                                    </ul>

                                    <h2>주차요금 및 감면안내</h2>
                                    <ul>
                                        <li>· 감면안내</li>
                                        <li>* 물적감면(경차, 저공해차 등) : 행정안전부 서버연계 차량번호 인식 후 자동감면<br />
                                            * 인적감면(장애인, 유공자, 다자녀 등) : 콜센터를 통해 감면증빙 확인 후 감면<br />
                                            * 주차요금 감면이 둘 이상 중복되는 경우 감면비율이 높은 1개항목만 적용됨</li>
                                    </ul>
                                    <div className='young_img'>
                                        <img src={process.env.PUBLIC_URL+"/img/gammung.png"} alt="img" />
                                    </div>
                                    <ul>
                                        <li>※ 긴급자동차(소방차, 구급차, 혈액공급차량, 그밖에 대통령령으로 정하는 자동차)의 경우 주차요금을 면제함<br />
                                            ※ 할인 미적용 시 주차장 내 호출버튼을 눌러 관제센터에 증빙 제시</li>
                                    </ul>

                                    <ul>
                                        <li>· 일반이용<br />
                                            * 감면대상자가 서류미비 등으로 일반요금으로 정산한 경우 30일 이내(주말 및 공휴일 포함) 환불신청서와 감면 증빙서류를 제출 시
                                            환불처리가 가능하며, 시간경과 후에는 환불이 불가함<br />
                                            · 정기권 이용<br />
                                            * 정기권 이용월 중 감면 자격이 변경된 경우에는 변경시점을 기준으로 차액을 다음달 청구금에서 차감 후 청구<br />
                                            * 이미 사용이 종료된 월 정기권에 대해서는 감면자격 소급적용이 불가함</li>
                                    </ul>
                                    <div className='young_img'>
                                        <img src={process.env.PUBLIC_URL+"/img/gammung2.png"} alt="img" />
                                    </div>


                                </div>
                            ) : (
                                <div className="young_faq">
                                    <h2>자주 묻는 질문(FAQ)</h2>
                                    <Table striped bordered hover className='no'>
                                        <thead>
                                            <tr>
                                                <th>질문</th>
                                                {/* <th>내용</th> */}
                                            </tr>
                                        </thead>
                                    </Table>
                                    <Accordion>
                                        {qna.slice((page[1] - 1) * 5, page[1] * 5).map((item, i) =>
                                            <Accordion.Item eventKey={i.toString()} key={i}>
                                                <Accordion.Header onClick={() => handleToggle(i.toString())}>
                                                    <span>Q.</span> {item.title}
                                                </Accordion.Header>

                                                <Accordion.Body>
                                                    <img src={process.env.PUBLIC_URL+item.body} alt={item.body} style={{ width: '100%', height: 'auto' }} />
                                                </Accordion.Body>
                                            </Accordion.Item>)}
                                    </Accordion>

                                    <Pagination size="sm">{items[1]}</Pagination>

                                </div>
                            )

                        )

                    }

                </div>
            </div>
        </>

    )
}

export default OpenSpace