import React, { useState } from 'react'
import '../MapSearch.scss';
import { Container, Table } from 'react-bootstrap'
import { useLocation, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';

const MapSearch = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const park = useSelector(state=>state.park)
  const [selectedPark, setSelectedPark] = useState(null);

  return (
    <>

      <div className="page_nav_wrap">
        <div className="page_hoem">
          <img src={process.env.PUBLIC_URL + "/img/home_icon.png"} alt="home" onClick={()=>navigate('/')}/>
        </div>
        <div className="page_one">
          <p onClick={()=>navigate('/map_search')}>주차장 찾기</p>
        </div>
        <div className="page_list">
          <p onClick={()=>navigate('/map_search')}>지도 검색</p>
        </div>
      </div>

      <div className="common_wrap">

        <aside className='left'>
          <div className="sidebar_wrap">
            <div className="sidebar_title">
              <p>공영주차장</p>
            </div>
            <div className="sidebar_list">
              <p className={location.pathname === '/map_search' ? 'active' : ''}
                onClick={() => navigate('/map_search')}>지도 검색</p>
              <p className={location.pathname === '/adress_search' ? 'active' : ''}
                onClick={() => navigate('/adress_search')}>주소 검색</p>
            </div>
          </div>
        </aside>

        <aside className='right'>
          <h4 className='mainTitle_jin'>공영주차장</h4>
          <p className='subTitle_jin'>지도검색</p>

          <div className="mapContents_jin">
            <div className="colors_jin">
              <div className="blue_jin"></div>
              <span>여유</span>
              <div className="yellow_jin"></div>
              <span>보통</span>
              <div className="red_jin"></div>
              <span>혼잡</span>
              <div className="black_jin"></div>
              <span>만차</span>
              <div className="grey_jin"></div>
              <span>현장운영</span>
            </div>
            <div className="map_jin">

              <div>
                <img className='redPark1_jin' src={process.env.PUBLIC_URL + "/img/parking-sign3.png"} alt="주차표시"
                onClick={() =>
                setSelectedPark(park.find(p => p.id === 1))} />

                <img className='yellowPark_jin' src={process.env.PUBLIC_URL + "/img/parking-sign1.png"} alt="주차표시"
                onClick={() =>
                setSelectedPark(park.find(p => p.id === 0))} />

                <img className='redPark2_jin' src={process.env.PUBLIC_URL + "/img/parking-sign3.png"} alt="주차표시"
                onClick={() =>
                setSelectedPark(park.find(p => p.id === 2))} />
              </div>

              {
                selectedPark && (
                  <div className="colorInf_jin">
                <img src={process.env.PUBLIC_URL + "/img/close-button.png"} alt="닫기버튼"
                onClick={() => setSelectedPark(null)} />

                <div className="box1_jin">
                  <h5>주차장 정보</h5>

                  <Table borderless>
                    <thead>
                      <tr>
                        <th colSpan={2}>{selectedPark.title}(부설)</th>
                      </tr>
                    </thead>

                    <tbody>
                      <tr>
                        <th>주소</th>
                        <td>{selectedPark.adress}</td>
                      </tr>
                      <tr>
                        <th>연락처</th>
                        <td>{selectedPark.phone}</td>
                      </tr>
                      <tr>
                        <th>운영시간</th>
                        <td>{selectedPark.openHours}</td>
                      </tr>
                      <tr>
                        <th>휴무일</th>
                        <td>없음</td>
                      </tr>
                    </tbody>
                  </Table>
                </div>

                <div className="box2_jin">
                  <h5>주차요금</h5>

                  <Table borderless>
                    <thead>
                      <tr>
                        <th>기본60분</th>
                        <th>30분당</th>
                      </tr>
                    </thead>

                    <tbody>
                      <tr>
                        <td>2,000원</td>
                        <td>1,000원</td>
                      </tr>
                    </tbody>
                  </Table>
                </div>

                <div className="box3_jin">
                  <h5>주차면수</h5>

                  <Table borderless>
                    <thead>
                      <tr>
                        <th>주차면수</th>
                        <th>주차가용대수</th>
                      </tr>
                    </thead>

                    <tbody>
                      <tr>
                        <td>{selectedPark.parkingSpace}</td>
                        <td>{selectedPark.parkingCount}</td>
                      </tr>
                    </tbody>
                  </Table>

                  <Table borderless>
                    <thead>
                      <tr>
                        <th>장애인</th>
                        <th>경차</th>
                        <th>전기충전면</th>
                      </tr>
                    </thead>

                    <tbody>
                      <tr>
                        <td>{selectedPark.disabledPreson}</td>
                        <td>{selectedPark.smCar}</td>
                        <td>{selectedPark.electricCharge}</td>
                      </tr>
                    </tbody>
                  </Table>
                </div>

              </div>
                )
              }
              
            </div>
          </div>
        </aside>

      </div>

    </>
  )
}

export default MapSearch