import React, { useState } from 'react'
import './Monthcash.scss';
import './Lee_common.scss';
import { useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router-dom';

const Monthcash = () => {
  const [inputCarNum, setInputCarNum] = useState('');
  const [searchedCar, setSearchedCar] = useState(null);
  const [notFound, setNotFound] = useState(false); // 차량 없음 상태 추가
  const carList = useSelector((state) => state.car);

  const normalize = (str) => str.replace(/\s+/g, '').toUpperCase();

  const handleSearch = () => {
    const result = carList.find(
      (car) => normalize(car.carNum) === normalize(inputCarNum)
    );
    setSearchedCar(result || null);
    setNotFound(!result); // 결과 없으면 true
  };

  const navigate = useNavigate();
  const location = useLocation();

  return (
    <div>
      <div className="page_nav_wrap">
        <div className="page_hoem">
          <img src={process.env.PUBLIC_URL + "/img/home_icon.png"} alt="home"
            onClick={() => navigate('/')} />
        </div>
        <div className="page_one">
          <p onClick={() => navigate('/month')}>정기권</p>
        </div>
        <div className="page_list">
          <p onClick={() => navigate('/monthcash')}>정기권 조회/결제</p>
        </div>
      </div>

      <div className="common_wrap">
        <aside className="left">
          <div className="sidebar_wrap">
            <div className="sidebar_title">
              <p>정기권</p>
            </div>
            <div className="sidebar_list">
              <p className={location.pathname === '/month' ? 'active' : ''}
                onClick={() => navigate('/month')}>정기권 안내</p>
              <p className={location.pathname === '/monthcash' ? 'active' : ''}
                onClick={() => navigate('/monthcash')}>정기권 조회/결제</p>
            </div>
          </div>
        </aside>

        <aside className="right">
          <div className="lee_Tcenter">
            <span className="lee_titleT lee_Tcenter">정기권 결제 조회/결제</span>
          </div>

          <div className="lee_cash_contentswrap">
            {/* 입력 영역 */}
            <div className="lee_cash_wrap">
              <div className="lee_cash_number">
                <div className="lee_cash_number_up">
                  <span className="lee_monthcash_titleT">차량 번호</span>
                  <span className="lee_monthcash_miniT">
                    공영주차 정기권 요금을 조회 / 결제를 하실 수 있습니다.
                  </span>
                </div>
                <div className="lee_cash_number_down">
                  <input
                    type="text"
                    placeholder="30바 5713"
                    value={inputCarNum}
                    onChange={(e) => setInputCarNum(e.target.value)}
                    className="car_input"
                  />
                </div>
                <button className="monthcash_btn1" onClick={handleSearch}>
                  정기권 조회
                </button>
                <div className="lee_cash_number_notice">
                  <span className="lee_cash_noticeT">유의사항</span>
                  <p className="lee_cash_noticesubT">
                    예시의 차량번호를 입력해보시오. 30바 5713, 22나 7217, 10마 3864, 43마 8964,
                    64가 1413, 10바 4222, 80바 6499, 20다 8424, 23사 8375, 83바 6795로 입력 가능합니다.
                  </p>
                </div>
              </div>
            </div>

            {/* 결과 영역 */}
            <div className="lee_cash_load">
              {notFound ? (
                <p className="lee_cash_noticesubT" style={{ textAlign: 'center', color: 'red' }}>
                  차량 정보를 찾을 수 없습니다.
                </p>
              ) : (
                <div>
                  <div className="lee_cash_load_up">
                    <div className="lee_cash_load_up1">
                      <span className="lee_cash_load_titleT">
                        {searchedCar ? searchedCar.entry.split(' ')[0] : '날짜'}
                      </span>
                      <span className="lee_cash_load_titleT">
                        {searchedCar ? searchedCar.location : '주차장명'}
                      </span>
                    </div>
                    <span className="lee_cash_load_subT">
                      {searchedCar ? searchedCar.carNum : '차량번호'}
                    </span>
                  </div>

                  <div className="lee_cash_load_down">
                    <div className="lee_cash_load_down_1">
                      <span className="lee_cash_load_titleT">할인 혜택 유형</span>
                      <span className="lee_cash_load_subT">
                        {searchedCar ? searchedCar.discounts || '-' : '-'}
                      </span>
                    </div>
                    <div className="lee_cash_load_down_1">
                      <span className="lee_cash_load_titleT">당첨된 정기권 유형</span>
                      <span className="lee_cash_load_subT">
                        {searchedCar?.pass?.type ?? '-'}
                      </span>
                    </div>
                    <div className="lee_cash_load_down_1">
                      <span className="lee_cash_load_titleT">결제 내역</span>
                      <span className="lee_cash_load_titleT">
                        {searchedCar?.pass?.price ?? '0'}원
                      </span>
                    </div>
                  </div>

                  <button className="monthcash_btn1">정기권 결제하기</button>
                </div>
              )}
            </div>
          </div>
        </aside>
      </div>
    </div>
  )
}

export default Monthcash;
