import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router-dom';
import { calculateCharge } from '../states/charge';
import './Advance.scss'


const Advance = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const location = useLocation();

    const carList = useSelector(state => state.car);
    const chargeList = useSelector(state => state.charge);

    const [searchValue, setSearchValue] = useState('');
    const [selectedCar, setSelectedCar] = useState(null);

    const handleSearch = () => {
        const result = carList.find(car => car.carNum.replace(/\s/g, '') === searchValue.replace(/\s/g, ''));
        setSelectedCar(result || alert('없는 번호 입니다'));
        if (result) dispatch(calculateCharge(result));
    };

    const matchedCharge = selectedCar && chargeList.find(c => c.id === selectedCar.id);

    const getUsageTime = (entry, exit) => {
        const start = new Date(entry);
        const end = new Date(exit);
        const diffMin = Math.floor((end - start) / 60000);
        const h = Math.floor(diffMin / 60);
        const m = diffMin % 60;
        return `${h}시간 ${m}분`;
    };

    return (
        <>
            <div className="page_nav_wrap">
                <div className="page_hoem">
                    <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home"
                    onClick={()=>navigate('/')} />
                </div>
                <div className="page_one">
                    <p onClick={()=>navigate('/advance_guide')}>주차요금 사전결제</p>
                </div>
                <div className="page_list">
                    <p onClick={()=>navigate('/advance')}>사전결제 조회/결제</p>
                </div>

            </div>

            <div className="common_wrap">
                <aside className='left'>
                    <div className="sidebar_wrap">
                        <div className="sidebar_title">
                            <p>주차요금 사전결제</p>
                        </div>
                        <div className="sidebar_list">
                            <p className={location.pathname === '/advance_guide' ? 'active' : ''}
                                onClick={() => { navigate('/advance_guide') }}>사전결제 안내</p>
                            <p className={location.pathname === '/advance' ? 'active' : ''}
                                onClick={() => { navigate('/advance'); }}>사전결제 조회/결제</p>
                        </div>
                    </div>
                </aside>

                <aside className='right'>
                    <div className="bang_title">
                        <p>사전 결제 조회/결제</p>
                    </div>

                    <div className="bang_advance_wrap">
                        <div className="bang_left">
                            <div className="bang_num">
                                <div className="bang_search_title">
                                    <p>차량 번호</p>
                                    <p>전체 차량 번호를 조회해 주세요. EX) 00가 0000 </p>
                                </div>
                                <div className="bang_search">
                                    <input
                                        type="text"
                                        value={searchValue}
                                        placeholder="ex) 00가 0000"
                                        onChange={(e) => setSearchValue(e.target.value)}
                                    />
                                </div>
                                <button className="btn_1" onClick={handleSearch}>조회</button>
                            </div>
                            <div className="bang_num_text">
                                <p>유의사항</p>
                                <p>예상 주차요금은 감면 및 할인 금액이 제외된 금액으로 결제시 금액과 상이할 수 있습니다.<br />
                                    사전 결제 후 30분 이내로 출차하지 않을시 추가 요금이 부과 됩니다.</p>
                                <p>예시 번호 : 30바 5713 , 22나 7217 , 43마 8964 , 64가 1413 , 83바 6795 , 10바 4222 , 80바 6499</p>
                            </div>
                        </div>

                        <div className="bang_right">
                            <div className="bang_num_check">
                                <div className="bang_num_content">
                                    <table>
                                        <tbody>
                                            <tr>
                                                <th><p>{selectedCar ? selectedCar.entry.split(' ')[0] : '-'}</p></th>
                                                <td><p>{selectedCar ? selectedCar.carNum : '-'}</p></td>
                                            </tr>
                                            <tr>
                                                <th><p>{selectedCar ? selectedCar.location : '-'}</p></th>
                                                <td><p>예상 주차 요금 : {matchedCharge ? `${(matchedCharge.parkingFee + matchedCharge.evFee).toLocaleString()} 원` : '-'}</p></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>

                                <div className="bang_num_detail">
                                    <table>
                                        <tbody>
                                            <tr>
                                                <th><p>입차</p></th>
                                                <td><p>{selectedCar ? selectedCar.entry : '-'}</p></td>
                                            </tr>
                                            <tr>
                                                <th><p>이용 시간</p></th>
                                                <td><p>{selectedCar ? getUsageTime(selectedCar.entry, selectedCar.exit) : '-'}</p></td>
                                            </tr>
                                            <tr>
                                                <th><p>전기차 충전</p></th>
                                                <td><p>{selectedCar && selectedCar.isEV && selectedCar.chargeTime > 0 ? `${selectedCar.chargeTime}분` : '이용 내역 없음'}</p></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>

                            <button
                                className='btn_1'
                                onClick={() => navigate('/payment', { state: { car: selectedCar, charge: matchedCharge } })}
                                disabled={!selectedCar}
                            >
                                사전 결제하기
                            </button>

                        </div>
                    </div>
                </aside>

            </div>
        </>);


};

export default Advance;