import { createSlice } from '@reduxjs/toolkit';

export const CHARGE_RULE = {
  BASE_PARKING_MIN: 60,            // 60분까지 기본요금
  BASE_PARKING_FEE: 2000,          // 60분까지 2000원
  EXTRA_PARKING_PER_30MIN: 1000,   // 60분 초과 시 30분당 1000원
  EV_CHARGE_PER_10MIN: 5000,       // 전기차 충전요금: 10분당 5000원
  DISCOUNT_RATE: 0.5               // 주차요금 50% 할인
};

const initialState = [];

export const charge = createSlice({
  name: 'charge',
  initialState,
  reducers: {
    calculateCharge: (state, action) => {
      const car = action.payload;
      const { id, entry, exit, isEV, chargeTime, discounts } = car;

      // 1. 이용시간 계산
      const start = new Date(entry);
      const end = new Date(exit);
      const durationMin = Math.ceil((end - start) / 60000);

      // 2. 주차요금 계산
      let parkingFee = CHARGE_RULE.BASE_PARKING_FEE;

      if (durationMin > CHARGE_RULE.BASE_PARKING_MIN) {
        const extraMin = durationMin - CHARGE_RULE.BASE_PARKING_MIN;
        const extraBlocks = Math.ceil(extraMin / 30);
        parkingFee += extraBlocks * CHARGE_RULE.EXTRA_PARKING_PER_30MIN;
      }

      // 3. 할인 적용 (주차요금에만)
      const hasDiscount = !!discounts;
      const discountedParkingFee = hasDiscount
        ? Math.floor(parkingFee * (1 - CHARGE_RULE.DISCOUNT_RATE))
        : parkingFee;

      // 4. 전기차 충전요금
      let evFee = 0;
      if (isEV && chargeTime > 0) {
        const chargeBlocks = Math.ceil(chargeTime / 10);
        evFee = chargeBlocks * CHARGE_RULE.EV_CHARGE_PER_10MIN;
      }

      // 5. 총요금 = 할인된 주차요금 + 전기차 요금
      const totalFee = discountedParkingFee + evFee;

      // 6. 상태 저장
      return [
        ...state.filter(item => item.id !== id),
        {
          id,
          durationMin,
          parkingFee,               // 원래 주차요금 (할인 전)
          discountedParkingFee,     // 할인된 주차요금
          evFee,
          hasDiscount,
          discountRate: hasDiscount ? CHARGE_RULE.DISCOUNT_RATE : 0,
          totalFee
        }
      ];
    }
  }
});

export const { calculateCharge } = charge.actions;
export default charge.reducer;
